/* Carousel Component Styles */
.carousel-container-HP {
    position: relative;
    width: 400px; /* Original width */
    height: 140px; /* Half of the original height */
    border: 1.5px solid black;
    border-radius: 20px;
    overflow: hidden;
    background-color: transparent;
    display: flex;
    justify-content: space-between;
    background-color: #f2f2f2 ;/* For arrow positioning */
}

.carousel-content-HP {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left:80px;
    position: absolute;
    width: 100%; /* Full width of the container */
    height: 100%; /* Full height of the container */
    will-change: transform; /* Optimizes animations */
}

.title-HP, .description-HP {
}

.title-HP {
    font-size: 24px; /* Adjust based on your requirement */
    color: black; /* Adjust color as needed */
    margin-bottom: 10px;
}

.description-HP {
    width:260px;
    font-size: 16px; /* Adjust based on your requirement */
    color: black; /* Adjust color as needed */
}

/* Assuming previous styles for carousel-container-HP and others remain */

.carousel-wrapper-HP {
    padding-top: 300px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.svg-container-HP {
    position: relative;
    width: 100%; /* Adjust the width as needed */
    height: 50px; /* Adjust the height to fit the SVG */
}

.carousel-svg-HP {
    position: absolute;
    top: -20px;
    left: 50%;
    transform: translateX(-50%);
    width: 300px; /* Adjust the size as needed */
    height: 300px;
}



/* Ensure .carousel-container-HP and other styles from previous setup remain unchanged */

.arrow-HP {
    cursor: pointer;
    font-size: 24px; /* Adjust based on your requirement */
    color: black; /* Adjust color as needed */
    user-select: none;
    z-index: 100;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    transition: font-size 0.1s ease; /* Smooth transition for font-size */
}

    .arrow-HP:hover {
        font-size: 28px; /* 1.3 times the original size */
    }

    .arrow-left-HP {
        left: 10px;
    }

.arrow-right-HP {
    right: 10px;
}

@media (min-width: 1000px) {
    .carousel-wrapper-HP {
        flex-direction: row-reverse;
        justify-content: center;
        align-items: center;
    }

    .carousel-container-HP{
        transform: translate(50%, -200px);
    }

    .carousel-svg-HP {
        transform: translate(-120%, -100px);
        width: 400px; /* Adjust size for larger screen layout */
        height: 400px; /* Adjust size for larger screen layout */
    }

    .carousel-container-HP, .carousel-svg-HP {
        margin: 0 20px; /* Space between container and SVG */
    }
}

