/* PostRectangles.css */
.posts-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px; /* Adjust the gap between rectangles as needed */
}

.galeria-plugin-title {
    color: #003946;
    position: relative;
    padding-top: 300px;
    left: 35%;
    font-size: 40px;
}

.post-rectangle {
    width: 300px; /* Fixed size */
    height: 200px; /* Fixed size */
    border: 1px solid black; /* Rounded black borders */
    border-radius: 10px; /* Adjust the border-radius as desired */
    display: flex;
    flex-direction: column;
    justify-content: start;
    padding: 10px;
    background-color: #f2f2f2;
}

.post-title {
    font-size: 20px; /* Adjust the font size as needed */
    font-weight: bold;
    margin-bottom: 5px;
    margin-left: 20px;
    transition: color 0.3s ease; /* Space between the title and the paragraph */
}

.post-arrow {
    margin-left: auto; /* Position arrow at the top-right */
    cursor: pointer;
    font-size: 50px;
    margin-right: 30px;
    margin-top: -10px; /* Adjusts the arrow slightly to the left */
    transition: transform 0.3s ease, color 0.3s ease; /* Change cursor on hover over the arrow */
}

/*.post-header:hover .post-title {
    color: darkorange;
}*/

.post-header:hover .post-arrow {
    transform: translateX(15px);
    color: darkorange;
    /* Moves the arrow slightly to the right on hover */
}

.post-intro {
    font-size: 14px; /* Adjust the font size as needed */
}

.post-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    cursor: pointer;
}

.veja-mais {
    position: absolute;
    top:1480px;
    font-size: 16px; /* Adjust as needed */
    cursor: pointer;
    left:60%;
    /* Adjust positioning as needed, depending on the layout */
}

.veja-mais-arrow {
    font-size: 20px;
    transition: transform 0.3s ease;
}

.veja-mais:hover .veja-mais-arrow {
    transform: translateX(10px);
    
}

