}
    /* Responsive design */
@media (max-width: 1100px) {

    

    .post-rectangle:nth-child(n+3) {
        display: none; /* Hide the third rectangle when screen width is <= 900px */
    }
}

@media (max-width: 800px) {
    .post-rectangle {
        display: none; /* Hide all rectangles initially */
    }

        .post-rectangle:first-child {
            display: flex; /* Only display the first rectangle when screen width is <= 600px */
        }
    
}


@media (max-width: 1000px) {

    .veja-mais {
        position: absolute;
        top: 1430px;
        font-size: 16px; /* Adjust as needed */
        cursor: pointer;
        left: 60%;
        /* Adjust positioning as needed, depending on the layout */
    }
}

