.container-servicos {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    height: 10vh;
    width:100%;
    margin: 150px 0 0 0; /* Adjusts container margin */
}

.servicos-main {
    height: 4050px;
}

.container-marcas {
    padding-top: 20px;
    display: flex;
    flex-wrap: wrap; /* Allows items to wrap */
    justify-content: center; /* Centers items horizontally */
    align-items: center; /* Aligns items vertically */
    gap: 20px; /* Space between items */
}

.t1-servicos {
    margin: 0 30px; /* Space between text elements */
    font-size: 20px; /* Initial font size */
    text-align: justify; /* Justifies text */
    width: 380px; /* Initial width */
}

.titulo-servicos-container {
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
}

.titulo-servicos {
    font-size: 32px;
    font-weight: 600;
    padding-top: 150px;
    text-align: center; /* Centers title */
    width: 700px;
}

.highlightOrange {
    color: orangered;
    font-size: 38px;
}


/* Adjustments at 1100px screen width */
@media (max-width: 1100px) {
    .titulo-servicos {
        width:600px;
        text-align:center;
        font-size: 28px; /* Adjusts title font size */
    }

    .highlightOrange {
        color: orangered;
        font-size: 31px;
    }

    .t1-servicos {
        margin: 0 20px; /* Adjusts side margins */
        font-size: 18px; /* Adjusts font size */
        width: 330px; /* Adjusts width */
    }
}


.links-container-services {
    display: grid;
    grid-template-columns: repeat(3, auto); /* Adjust column sizing to content */
    justify-content: center; /* Center the grid horizontally */
    gap: 20px 7vw; /* Vertical gap of 20px and custom horizontal gap of 40px */
    margin-top: 200px;
    justify-items: center; /* Space between the text blocks and links */
}

.scroll-link-services {
    display: inline-block; /* Allows setting dimensions */
    margin: 5px; /* Adds some space around the links for better separation */
    color: #000; /* Text color */
    text-decoration: none; /* Remove default underline */
    border-bottom: 2px solid #000; /* Underline effect with border */
    text-align: center; /* Ensure text is centered */
    line-height: 1.5; /* Increases height without affecting border position */
    cursor: pointer; /* Changes the cursor to indicate clickable items */
}

.centered-link-container {
    display: flex;
    justify-content: center;
    margin-top: 20px; /* Adjust to match the spacing of your existing links */
}



@media (max-width: 1100px) {
    .servicos-main {
        height: 3850px;
    }

    
}



    /* Adjustments for stacking .t1-servicos vertically below 750px */
@media (max-width: 750px) {
    .container-servicos {
        margin-top: 250px;
        margin-bottom: 50px;
        flex-direction: column; /* Stacks children vertically */
    }

    .links-container-services {
        padding-top: 75px;
    }

    .titulo-servicos {
        width: 400px;
        text-align: center;
        font-size: 24px; /* Ensures title font size is consistent */
    }

    .highlightOrange {
        color: orangered;
        font-size: 27px;
    }

    .t1-servicos {
        width: 600px;
        margin: 10px 0; /* Adjusts margin for vertical stacking */
        font-size: 18px; /* Ensures font size is consistent */
        width: 400px; /* Ensures width is consistent */
    }
}

@media (max-width: 700px) {
    .servicos-main {
        height: 6100px;
    }

    
}