.footer-container-services {
    position: absolute;
    top: 3800px;
    left: 0;
    right: 0;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; /* Align children to the bottom */
    padding: 20px;
    background-color: transparent;
    min-height: 200px; /* Define a minimum height for the footer */
}

.footer-website-name-services img {
    height: 100px;
    width: 100px;
}

.footer-website-name-services {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    align-self: flex-end; /* Adjust if you want the name/logo at the bottom */
}

.footer-links-services {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-self: flex-end;
    padding-bottom: 30px; /* Ensure links are positioned at the bottom */
}



.footer-contact-inf-services {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-self: flex-end;
    text-align: right; /* Ensure contact info is positioned at the bottom right */
}

.footer-social-icons-services {
    display: flex;
    justify-content: space-between;
    width: 20vw;
    min-width: 150px;
    max-width: 150px;
    margin-top: 10px;
}

.footer-link-services, .footer-contact-item-services, .footer-website-name-services h1 {
    text-decoration: none;
    color: #000;
}

.footer-contact-item-services {
    font-size: 0.9rem;
}

.footer-website-name-services h1 {
    font-size: 35px;
}






@media (max-width: 1100px) {

    .footer-container-services {
        top: 3600px;
    }
}

@media (max-width: 700px) {
    .footer-container-services {
        top: 5850px;
    }

    .footer-links-services {
        display: flex;
        flex-direction: column;
        gap: 10px;
        align-self: flex-end;
        padding-bottom: 30px; /* Ensure links are positioned at the bottom */
    }

    .footer-website-name-services {
        position: absolute;
        top: -60px;
        left: 32vw;
        font-size: 35px;
    }

        .footer-website-name-services h1 {
            font-size: 24px;
        }

        .footer-website-name-services img {
            height: 60px;
            width: 60px;
        }

    .footer-contact-info-services {
        display: flex;
        flex-direction: column;
        gap: 5px;
        align-self: flex-end;
        text-align: right; /* Ensure contact info is positioned at the bottom right */
    }

    .footer-social-icons-services {
        display: flex;
        justify-content: space-between;
        width: 20vw;
        min-width: 150px;
        max-width: 150px;
        margin-top: 10px;
    }
}
