.QS-images-container {
    display: flex;
    flex-direction: column;

    z-index: 3; /* Ensure images are above the circle */
    overflow-x:hidden;
}

.flex-container-QS {
    display: flex;
    justify-content:center;
}

.quem-somos-image1 {
    width: 800px;
    height: auto;
    border-radius: 10px;
    object-fit: cover;
    position: relative;
    margin-top: 850px;
}



.quem-somos-image0 {
    width: 650px;
    height: auto;
    border-radius: 10px;
    position: relative;
    margin-top: 465px;
    left: 50vw;

}

@media (max-width: 1400px){








    .quem-somos-image0 {
        width: 550px;
        height: auto;
    }
}

@media (max-width: 1200px) {






    .quem-somos-image1 {
        width: 600px;
        height: auto;
        border-radius: 10px;
        margin-top: calc(10vw + 600px);
    }

    .quem-somos-image0 {
        width: 120%;
        height: auto;
        border-radius: 0px;
        object-fit: cover;
        position: relative;
        left:0;
        margin-top: 60px;
    }
}

@media (max-width: 750px) {
    .quem-somos-image1 {
        width: 500px;
        margin-top: calc(10vw + 630px);
    }


}

@media (max-width: 600px) {
    .quem-somos-image1 {
        width: 400px;
        margin-top: calc(10vw + 730px);
    }
}

