.footer-container-HP {
    margin-top: 200px;
    display: flex;
    justify-content: space-between;
    align-items: flex-end; /* Align children to the bottom */
    padding: 20px;
    background-color: transparent;
    min-height: 200px; /* Define a minimum height for the footer */
}

.footer-website-name-HP img {
    height: 100px;
    width: 100px;
}

.footer-website-name-HP {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    align-self: flex-end; /* Adjust if you want the name/logo at the bottom */
}

.footer-links-HP {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-self: flex-end;
    padding-bottom: 30px; /* Ensure links are positioned at the bottom */
}

.footer-contact-info-HP {
    display: flex;
    flex-direction: column;
    gap: 5px;
    align-self: flex-end;
    text-align: right; /* Ensure contact info is positioned at the bottom right */
}

.footer-social-icons-HP {
    display: flex;
    justify-content: space-between;
    width: 20vw;
    min-width: 150px;
    max-width: 150px;
    margin-top: 10px;
}

.footer-link-HP, .footer-contact-item-HP, .footer-website-name-HP h1 {
    text-decoration: none;
    color: #000;
}

.footer-contact-item-HP {
    font-size: 0.9rem;
}

.footer-website-name-HP h1 {
    font-size: 35px;
}

