.accordion-container {
    width: 80%;
    max-width: 800px;
    margin: 20px auto;
    overflow: hidden;
    border-top: 1px solid darkgrey; /* Top border */
    border-bottom: 1px solid darkgrey; /* Bottom border */
}

.accordion-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 15px;
    cursor: pointer;
    /* Removed border-radius for sharp corners */
}

.accordion-title {
    margin: 0;
    font-size: 1.2rem;
}

.accordion-arrow {
    transition: transform 0.5s ease;
}

.rotate {
    transform: rotate(180deg);
}

.accordion-content {
    transition: max-height 1s ease;
    overflow: hidden;
    max-height: 0; /* Start with content collapsed */
    padding: 0 15px 0px 15px; 
    text-align:justify;/* Adjust padding as needed */
    /* Ensures only the bottom border is visible when expanded */
}

.accordion-expanded .accordion-content{
    padding-bottom:10px;
}
