.custom-contact-form-container {
    position: absolute;
    top: 2400px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: auto;
    width: 100%;
    background-color: transparent;
}

.custom-form-inputs, .custom-buttons-containerC {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.custom-form-inputs {
    align-items: flex-end;
    padding-right: 10px;
}

.custom-buttons-containerC {
    align-items: flex-start;
    padding-left: 10px; /* Half of your desired gap */
}

.custom-buttons-rowC{
    display:flex;
    gap:10px;
    align-content:center;
    justify-content:center;
}

.custom-form-inputs input,
.custom-form-inputs textarea,
.custom-buttonC {
    width: auto; /* Adjust this as necessary */
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.custom-form-inputs input,
.custom-form-inputs textarea {
    width: 400px;
}

.custom-buttonC {
    background-color: transparent;
    color: black;
    border: 1px solid black;
    border-radius: 13px;
    cursor: pointer;
}

    .custom-buttonC-toggled,
    .custom-buttonC:hover {
        background-color: black;
        color: white;
    }

/* Keep your media queries as they are, assuming they're set up for responsive adjustments */

@media (max-width: 1400px) {
    .custom-contact-form-container {
        top: 2345px;
    }
}

@media (max-width: 1200px) {
    .custom-contact-form-container {
        top: calc(90vw + 1950px);
        padding-left: 0px;
        padding-right: 0px;
    }

    .custom-form-inputs input,
    .custom-form-inputs textarea {
        width: calc(70vw - 340px);
    }

}

@media (max-width: 900px) {
    .custom-contact-form-container {
        flex-direction: column;
        align-items: center;
        top: calc(80vw + 2200px);
        padding-left: 0px;
    }

    .custom-form-inputs,
    .custom-buttons-containerC {
        width: 600px;
        max-width: 90%;
        align-items: center;
        padding-left: 0px;
    }

        .custom-form-inputs input,
        .custom-form-inputs textarea {
            width: 95%;
            max-width: 1000px;
        }

    .custom-buttons-containerC {
        padding-left: 0px;
        padding-top: 100px; /* Adjust based on your spacing needs */
    }
}

@media (hover: none){
    .custom-buttonC:hover {
        background-color: unset;
        color: unset;
    }
}
