/* Base styles */
/* Base styles */
.react-rectangle-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px; /* Fixed gap between rectangles */
    flex-wrap: wrap; /* Allows rectangles to wrap in smaller views */
}

.react-rectangle {
    width: 300px; /* Initial width, ensuring it fits three per row */
    min-height: 600px; /* Initial min-height to accommodate content */
    background-color: #cfd0e8;
    box-shadow: 4px 8px 8px rgba(0, 0, 0, 0.1), -4px -8px 8px rgba(0, 0, 0, 0.1);
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between; /* Adjusted to space content evenly */
    padding: 20px;
    margin: 10px; /* Ensure some space around rectangles */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
    position: relative; /* Added for absolute positioning inside */
}

.react-logo {
    width: auto;
    height: 50px; /* Adjust height accordingly */
    margin-bottom: 10px; /* Spacing below the logo */
}

.react-title {
    font-size: 1.3rem;
    font-weight: bold;
    position: absolute; /* Fixed position */
    top: 60px; /* Reduced spacing for all textual content */
}

.react-subtitle {
    text-align:justify;
    font-size: 1rem;
    font-style: italic;
    position: absolute; /* Fixed position */
    top: 120px; /* Fixed vertical position */
    width: 90%; /* Ensure it spans the width of the container */
}

.react-button {
    position: absolute; /* Fixed position */
    top: 350px; /* Adjust the position as needed */
    padding: 10px 20px;
    border-radius: 20px;
    border: 2px solid #000;
    align-self: center; /* Center the button horizontally */
    cursor: pointer;
}

.react-service-list {
    list-style: none;
    padding: 0;
    margin: 0; /* Remove default margin */
    position: absolute; /* Adjusted to align at the bottom */
    bottom: 20px; /* Bottom aligned */
    width: 100%; /* Ensure it spans the width of the container */
}

.react-service-item {
    display: flex;
    align-items: center;
    gap: 5px; /* Reduced gap between checkmark and service text */
    margin-bottom: 5px; /* Spacing for each item */
}

.react-checkmark {
    color: green;
}

/* Media query for screen sizes below 1100px */
@media (max-width: 1100px) {
    .react-rectangle-container {
        gap: 15px; /* Reduced gap between rectangles */
    }

    .react-rectangle {
        width: 250px; /* Adjust 20px if your gap is different */
        height: auto; /* Allows height to adjust based on content */
        min-height: 600px; /* Minimum height to ensure content fits */
        padding: 15px; /* Scaled down padding inside each rectangle */
        box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.1), -2px 0 4px rgba(0, 0, 0, 0.1), 0 4px 8px rgba(0, 0, 0, 0.1);
    }

    .react-logo {
        height: 37.5px; /* Scaled down logo size */
    }

    .react-title {
        font-size: 1.125rem; /* Scaled down */
    }

    .react-subtitle, .react-service-item {
        font-size: 0.875rem; /* Scaled down */
    }

    .react-button {
        padding: 8px 16px; /* Slightly smaller padding */
        margin: 8px 0; /* Reduced margin */
        font-size: 0.875rem; /* Scaled down font size */
    }

    .react-service-item {
        gap: 4px; /* Further reduced gap */
        margin-bottom: 3px; /* Reduced spacing between list items */
    }
}

