/* Adjustments for single column layout */
/* Adjustments for single column layout */
@media (max-width: 880px) {

    .react-button {
        top:250px; /* Scaled down font size */
    }

    .react-rectangle {
        width: 80vw;
        height: auto; /* Adjust height based on content */
        padding: 10px; /* Tighten the layout */
        display: flex;
        flex-direction: column;
        min-height: 400px;
        justify-content: space-between; /* Space content evenly */
    }

    .react-service-list {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between; /* Ensure two columns of services */
        list-style: none;
        padding: 0;
        width: 100%; /* Ensure the list uses the full width of the rectangle */
    }

    .react-service-item {
        width: 48%; /* Adjust so two items fit side by side */
        text-align: left; /* Align text as needed */
    }
}

